<template>
  <div class="EtmScreenTab-wrap">
    <span
      v-if="title"
      class="screenTabTitle"
    >
      {{ title }}:
    </span>
    <ul>
      <li
        v-for="(item, index) in options"
        :key="index"
        :class="{
          active: checkActive(item.value),
          'is-multiple': multiple,
          'not-click': disabled || ( !disabled && item.disabled ),
        }"
        @click="optionsClick(item)"
      >
        {{ item.name }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'EtmScreenTab',
  model: {
    prop: 'currentValue',
    event: 'input'
  },
  props: {
    currentValue: { // 
      type: null,
      required: true
    },
    options: { // 
      type: Array,
      required: true
    },
    multiple: { // 
      type: Boolean,
      default: false
    },
    disabled: { // 
      type: Boolean,
      default: false
    },
    title: { // 
      type: String,
      default: ''
    }
  },
  methods: {
    optionsClick(item) {
      if (this.disabled) {
        return
      } else {
        if (item.disabled) {
          console.log(item.disabled)
          return
        }
      }

      if (this.multiple === false) { // 
        // 
        this.$emit('input', item.value)
        this.$emit('change', item.value)
      } else { // 
        if (this.currentValue.indexOf(item.value) === -1) {
          // push
          this.currentValue.push(item.value)
        } else {
          // 
          this.currentValue.splice(this.currentValue.indexOf(item.value), 1)
        }
        this.$emit('change', this.currentValue)
      }
    },
    checkActive(val) {
      if (this.multiple === false) {
        return this.currentValue === val
      }
      return this.currentValue.indexOf(val) !== -1
    }
  }
}
</script>

<style lang="scss">
@import "../../styles/src/etm/screenTab";

</style>
